.products-section{
    background-color: $white-text-color;
}

.about-products{
    padding-top: 120px;
    padding-bottom: 100px;

    @media screen and (min-width: 768px){
        padding-bottom: 110px;
    }

    @media screen and (min-width: 1200px){
        padding-top: 125px;
        padding-bottom: 120px;
    }
}

.our-products{
    margin-top: 145px;

    text-align: center;

    @media screen and (min-width: 768px){
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;

        margin-top: 150px;
    }

    @media screen and (min-width: 1200px){
        margin-top: 260px;
    }
}

.product-photo{
    position: absolute;
    top: 0%;
    left: 50%;
    transform: translate(-50%, -45%);

    @media screen and (min-width: 1200px){
        transform: translate(-50%, -65%);
    }
}

.our-products .list-item{
    margin-right: auto;
    margin-left: auto;

    width: 280px;

    border-radius: 24px;
   
    @media screen and (min-width: 768px) {
        width: calc((100% - 3 * 20px) / 3);
    }

    &:not(:last-child){
        @media screen and (max-width: 767px) {
            margin-bottom: 130px;
        }

        @media screen and (min-width: 768px) {
            margin-right: 20px;
        }

        @media screen and (min-width: 1200px) {
            margin-right: 25px;
        }
    }

    & span{
        display: inline-block;

        margin-top: 30px;
    
        font-family: $accent-font-family;
        font-size: 20px;
        font-weight: 900;
        line-height: 1.15;
        letter-spacing: 0.2em;
        text-transform: uppercase;
        color: $white-text-color;
    }
}

.product-name{
    margin-top: 0;
    margin-bottom: 0;

    font-family: $accent-font-family;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $white-text-color;
}

.product-details{
    margin-top: 30px;
    margin-bottom: 0;
    font-family: $primary-font-family;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    letter-spacing: 0.04em;
    color: $white-text-color;
}

.product-button{
    margin-top: 30px;
    padding: 0;
    border: none;
    border-radius: 50%;

    width: 40px;
    height: 40px;

    @media screen and (min-width: 768px) {
        margin-top: 40px;
    }

    @media screen and (min-width: 1200px) {
        margin-top: 60px;
    }
}

.ice-cream{
    position: relative;
    background-color: $hero-background-color;
}

.ice-coffee{
    position: relative;
    background-color: $beige-bgr-color;
}

.milkshakes{
    position: relative;
    background-color: $pistachio-bgr-color;
}

.product-card{
    padding: 160px 40px 40px 40px;

    @media screen and (min-width: 768px) {
        padding: 150px 10px 40px 10px;
    }

    @media screen and (min-width: 1200px) {
        padding: 165px 45px 50px 45px;
    }
}